import {AbstractControl, ValidatorFn} from '@angular/forms';
import {CommonsUtils, DateUtils} from '@er/utils';

export const dateBeforeValidator = function(before: string | Date): ValidatorFn {
  const fn = (control: AbstractControl) => {
    const value = control.value;
    if (!DateUtils.isDate(value)) {
      return {'dataBefore': {message: 'éè¯¯çæ¥ææ ¼å¼', value}};
    }
    const isBefore = CommonsUtils.isBefore(control.value, before);
    return isBefore ? null : {'dataBefore': {value, before}};
  };
  return fn;
};

