import {AfterViewInit, ComponentFactoryResolver, Directive, HostBinding, Input, Optional, ViewContainerRef} from '@angular/core';
import {NgControl} from '@angular/forms';


/* tslint:disable:directive-selector */
@Directive({
  selector: '[formControlName],[formControl],[ngModel]'
})
export class ValidateStateDirective implements AfterViewInit {

  @Input() messages: { [key: string]: string };

  @Input() showError = true;

  @Input() enableValidation = true;

  currentState: string;

  constructor(private componentFactoryResolver: ComponentFactoryResolver,
              private viewContainerRef: ViewContainerRef,
              @Optional() private ngControl: NgControl) {
  }

  get control() {
    if (this.ngControl && this.ngControl.control) {
      return this.ngControl.control;
    } else {
      return undefined;
    }
  }

  @HostBinding('class.er-valid')
  get isValid(): boolean {
    if (!this.showError || (!this.ngControl && this.ngControl.control == null)) {
      return false;
    }
    return this.ngControl && this.ngControl.control && this.ngControl.control.valid;
  }

  ngAfterViewInit() {
    if (!this.ngControl || !this.enableValidation) {
      return;
    }

    const attrs: NamedNodeMap = this.viewContainerRef.element.nativeElement.attributes;
    if (attrs) {
      const tag = !!attrs.getNamedItem('noerror');
      if (this.showError && tag) {
        this.showError = false;
        return;
      }
    }
  }

}
