import {ChangeDetectionStrategy, Component, ContentChild, Input, OnDestroy, OnInit, Optional, TemplateRef} from '@angular/core';
import {ControlContainer, FormGroup, FormGroupDirective} from '@angular/forms';
import {CommonsUtils} from '@er/utils';
import {Subject, Subscription} from 'rxjs';
import {ValidateUtils} from '../../utils';


@Component({
  selector: 'er-form-validate-messages',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FormValidateMessagesComponent implements OnInit, OnDestroy {

  @Input() form: FormGroup;

  @Input() messages: { [key: string]: string };

  @Input() fields: {};

  @Input() styleClass: string;

  @ContentChild(TemplateRef) errorMsgTpl: TemplateRef<any>;

  errors$: Subject<{}> = new Subject();

  subscription: Subscription;

  constructor(@Optional() private controlContainer: ControlContainer) {
  }

  checkFormValidate() {
    if (this.form.invalid) {
      alert();
      this.errors$.next(ValidateUtils.getFormErrors(this.form, this.messages, this.fields));
    } else {
      this.errors$.next(undefined);
    }
  }

  ngOnInit(): void {
    if (!this.form && this.controlContainer) {
      this.form = this.controlContainer.formDirective ? (this.controlContainer.formDirective as FormGroupDirective).form : null;
    }
    if (this.form) {
      this.subscription = this.form.statusChanges
        .subscribe(() => {
          this.checkFormValidate();
        });
    }
  }

  errorKeys(errors: {}) {
    return Object.keys(errors);
  }

  errorMsg(error) {
    return CommonsUtils.getText(error);
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
