import {AbstractControl, ValidatorFn} from '@angular/forms';
import {CommonsUtils, DateUtils} from '@er/utils';

export const dateAfterValidator = function (after): ValidatorFn {
  const fn = (control: AbstractControl) => {
    const value = control.value;
    if (!DateUtils.isDate(value)) {
      return {'dateAfter': {message: 'éè¯¯çæ¥ææ ¼å¼', value}};
    }
    const isAfter = CommonsUtils.isAfter(value, after);
    return isAfter ? null : {'dateAfter': {value, after}};
  };
  return fn;
};
