import {AbstractControl, FormGroup, ValidatorFn} from '@angular/forms';
import {FormUtils} from '@er/utils';


export const equalToValidator = function (controlName: string): ValidatorFn {
  const fn = (control: AbstractControl) => {
    let parent = control.parent;
    let target: AbstractControl;
    while (parent instanceof FormGroup) {
      target =  FormUtils.getFormControl(parent, controlName);
      if (target) { break; }
      parent = parent.parent;
    }
    if (target) {
      const value = control.value;
      const targetValue = target.value;
      const isValid = value === targetValue;
      return isValid ? null : {'equalTo': {value, controlName}};
    }
    return null;
  };
  return fn;
};
