import {AbstractControl, FormArray, FormControl, FormGroup, ValidationErrors} from '@angular/forms';
import {CommonsUtils} from '@er/utils';
import {NgValidateMessages} from '../validators/all';


export class ValidateUtils {

  static getFormErrors(form: AbstractControl, messages?: { [key: string]: string }, formlyfield?: any) {
    const errors = {};
    if (form instanceof FormControl) {
      return (ValidateUtils.getControlErrors(<FormControl>form, messages, formlyfield));
    } else if (form instanceof FormGroup) {
      Object.keys(form.controls).map(name =>
        errors[name] = (ValidateUtils.getFormErrors(<FormGroup>form.controls[name], messages, formlyfield))
      );
    } else if (form instanceof FormArray) {
      const err = [];
      (<FormArray>form.controls[name]).controls.forEach(control =>
        err.push((ValidateUtils.getFormErrors(control, messages, formlyfield)))
      );
      return err;
    }
    return errors;
  }

  static getControlErrors(control: FormControl, messages?: { [key: string]: string }, formlyfield?: any) {
    return ValidateUtils.getErrorMessage(control.errors, messages, formlyfield);
  }

  static getErrorMessage(errors: ValidationErrors, messages?: { [key: string]: string }, formlyfield?: any) {
    if (!errors) {
      return {};
    }
    const errorMessages = {};
    Object.keys(errors).forEach(key => {
      let message;
      if (formlyfield) {
        if (formlyfield.validators && formlyfield.validators[key] && formlyfield.validators[key].message) {
          message = formlyfield.validators[key].message;
        } else if (formlyfield.asyncValidators && formlyfield.asyncValidators[key] && formlyfield.asyncValidators[key].message) {
          message = formlyfield.asyncValidators[key].message;
        }
      }
      if (!message) {
        if (messages && messages.hasOwnProperty(key)) {
          message = messages[key];
        } else if (NgValidateMessages.hasOwnProperty(key)) {
          message = NgValidateMessages[key];
        } else {
          message = key;
        }
      }
      if (typeof message === 'string') {
        errorMessages[key] = message;
      } else if (typeof message === 'function') {
        errorMessages[key] = message.apply(this, [errors[key], formlyfield]);
      }
    });
    return errorMessages;
  }

  static dynaValidationMessageFn(
    error,
    context?: { errorKey?: string, format?: string, errorProps?: string | string[], formlyField?: any }
  ) {
    let values = [];
    if (CommonsUtils.isJson(error) && context.errorProps) {
      if (Array.isArray(context.errorProps)) {
        context.errorProps.forEach(key => values.push(error[key]));
      } else {
        values.push(error[context.errorProps]);
      }
    } else {
      const dyna: any[] = CommonsUtils.get(context, 'validation.dyna', []);
      const info = CommonsUtils.find(dyna, {key: context.errorKey});
      if (info) {
        values = info['args'] || [];
      }
    }
    return CommonsUtils.templateStr(context.format, values);
  }
}
