import {AbstractControl, ValidatorFn} from '@angular/forms';
import {CommonsUtils, DateUtils} from '@er/utils';

export const dateBetweenValidator = function(before, after): ValidatorFn {
  const fn =  (control: AbstractControl) => {
    const value = control.value;
    if (!DateUtils.isDate(value)) {
      return {'dataBetween': {message: 'éè¯¯çæ¥ææ ¼å¼', value}};
    }
    const isBetween = CommonsUtils.isBefore(value, before)
      && CommonsUtils.isAfter(value, after);
    return isBetween ? null : {'dataBetween': {value, before, after}};
  };
  return fn;
};

