import {AbstractControl, ValidatorFn} from '@angular/forms';
import {CommonsUtils, RegUtils} from '@er/utils';

export const matchValidator = function (pattern: string | string[] | RegExp | RegExp[], errorKey: string = 'match'): ValidatorFn {
  const fn = (control: AbstractControl) => {
    const value = control.value;
    if (CommonsUtils.isEmptyString(value)) {
      return null;
    }
    return RegUtils.test(value, pattern) ? null : {[errorKey]: {pattern, value}};
  };
  return fn;
};